/*
defaults
*/
*{
    box-sizing: border-box;
}
button{
    border: none;
    font-size: 18px;
    height: 40px;
    border-radius: 20px;
    padding: 10px 20px;
}
input, select{
    font-size: 18px;
    height: 30px;
}
li{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: black;
}
.movies{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}
.movies li{
    width: 40%;
    padding: 5px;
    gap: 5px;
    border: 1px solid gainsboro;
}